.mainWrapper {
    height: 100vh;
    background-color: black;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
    font-family: "Lucida Sans";
}
.mainContactPage{
    width: 70vw;
    height: auto;
    display: grid;
    grid-template-rows: 100px 1fr 100px;
    grid-template-columns: 1fr;
    border: 3px solid black;
    align-items: center;
    justify-items: center;
}
.contactText {
    align-self: flex-start;
    background-color: green;
    font-size: 25px;
}
.mainContactText{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2,1fr);
    align-self: center;
    justify-self: center;
    border: 3px solid black;
    text-align: center;
    width: 80%;
}
.mainContactText input, textarea{
    background-color: whitesmoke;
    width: 80%;
    height: 50px;
    margin: 15px;
    justify-self: center;
    font-size: 20px;
    text-align: center;
}
.mainContactText textarea{
    height: auto;
}
.contactButton{
    align-self: center;
}
@media screen and (max-width: 800px) {
    .mainWrapper {
        height: auto;
        padding-bottom: 100px;
    }
    .mainContactPage{
        width: 80vw;
        height: auto;
        display: grid;
        grid-template-rows: 100px 1fr 100px;
        grid-template-columns: 1fr;
        border: none;
        align-items: center;
        justify-items: center;
    }
    .contactText {
        align-self: center;
        background-color: green;
        font-size: 25px;
    }
    .mainContactText{
        width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2,1fr);
        align-self: center;
        justify-self: center;
        border: 3px solid black;
        margin: 50px 0;
        text-align: center;
    }
}