}
button {
    text-transform: uppercase;
    text-shadow: 0 0 15px #07e8ff;
    letter-spacing: 10px;
    font-size: 20px;
    color: #07e8ff;
    border: none;
    padding: 5% 10%;
    background-color: transparent;
    cursor: pointer;
    box-shadow: 0 10px 30px rgba(252,252,252,.6);
    position: relative;
    overflow: hidden;
    border-radius: 15px;
    width: 230px;
    height: 50px;
}
button:before {
    content: "";
    position: absolute;
    top: 3px;
    left: 3px;
    bottom: 3px;
    width: 50%;
    background-color: rgba(255,255,255,0.05);
}
    button>span:nth-child(1),button>span:nth-child(2),button>span:nth-child(3),button>span:nth-child(4){
    position: absolute;
}
    button:hover>span:nth-child(n){
            -webkit-animation-play-state: running;
                    animation-play-state: running;
    }
    button>span:nth-child(1){
    top: 0;
        left: 0;
        width: 100%;
        height: 3px;
        background: -webkit-gradient(linear, left top, right top, from(black),color-stop(#07e8ff),to(black));
        background: linear-gradient(90deg, black,#07e8ff,black);
        -webkit-animation: animateTop 2s linear infinite paused;
                animation: animateTop 2s linear infinite paused;
}
    button>span:nth-child(2){
    top: 0;
        right: 0;
        width: 3px;
        height: 100%;
        background: -webkit-gradient(linear, left top, left bottom, from(0), color-stop(black),color-stop(#07e8ff),to(black));
        background: linear-gradient(0, black,#07e8ff,black);
        -webkit-animation: animateRight 2s linear infinite paused;
                animation: animateRight 2s linear infinite paused;
}
    button>span:nth-child(3){
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: -webkit-gradient(linear, left top, right top, from(black),color-stop(#07e8ff),to(black));
    background: linear-gradient(90deg, black,#07e8ff,black);
    -webkit-animation: animateBottom 2s linear infinite paused;
            animation: animateBottom 2s linear infinite paused;
}
button>span:nth-child(4){
    top: 0;
    left: 0;
    width: 3px;
    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(0), color-stop(black),color-stop(#07e8ff),to(black));
    background: linear-gradient(0, black,#07e8ff,black);
    -webkit-animation: animateLeft 2s linear infinite paused;
            animation: animateLeft 2s linear infinite paused;
}
@-webkit-keyframes animateTop{
    0%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
        100%{
            -webkit-transform: translateX(100%);
                    transform: translateX(100%);
        }
    }
@keyframes animateTop{
    0%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
        100%{
            -webkit-transform: translateX(100%);
                    transform: translateX(100%);
        }
    }
@-webkit-keyframes animateRight{
    0%{
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
        100%{
            -webkit-transform: translateY(100%);
                    transform: translateY(100%);
        }
    }
@keyframes animateRight{
    0%{
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
        100%{
            -webkit-transform: translateY(100%);
                    transform: translateY(100%);
        }
    }
@-webkit-keyframes animateBottom{
    0%{
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    100%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}
@keyframes animateBottom{
    0%{
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    100%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}
@-webkit-keyframes animateLeft{
    0%{
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    100%{
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
}
@keyframes animateLeft{
    0%{
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    100%{
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
}
